
body, html{
  margin: 0;
  height: 100%;
  overflow: hidden;
}
.noselect{
   -webkit-user-select: none;
      -moz-user-select: none;
       -ms-user-select: none;
           user-select: none;
}
.app-container{
  background-color: #f1f1f1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
  /*must have to stop jumping when clicking*/
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end; 
  /*dont align items center to avoid vertical jumping*/
  /*align-items: center;*/
  /*flex-wrap: wrap;*/
}
#app {
  font-family: 'Raleway', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.sheet{
  position: relative;
  float: left;
  display: inline;
  /*width: 80%;*/
  background-color: #c6cad2;
}
.circle{
  position: absolute;
  display: block;
  /*border: 2px dotted white;*/
  border-radius: 50%;
  background-color: rgba(255,255,255,0.6);
  mix-blend-mode: overlay;
  z-index: 0;
  /*transition: 1s ease-in-out;*/
  /*transition-property: transform;*/
  /*transition-delay: 300ms;*/
  transition: background-color 0.1s ease-out, opacity 0.2s ease-out, box-shadow 0.1s ease-out, -webkit-transform 0.1s ease-out;
  transition: transform 0.1s ease-out, background-color 0.1s ease-out, opacity 0.2s ease-out, box-shadow 0.1s ease-out;
  transition: transform 0.1s ease-out, background-color 0.1s ease-out, opacity 0.2s ease-out, box-shadow 0.1s ease-out, -webkit-transform 0.1s ease-out;
  left: 0;
  top: 0;

  box-shadow: 0px 0px 60px 0px rgba(0,0,0,0.3);

  /*box-sizing: content-box;*/
  /*mix-blend-mode: overlay;*/
  /*background-clip: border-box;*/
  pointer-events: none;
}
.mulitply.off, .circle.off{
  /*opacity: 0 !important;*/
  display: none;
}
.multiply{
  /*box-shadow: 0px 0px 20px 0px #000;*/
  /*opacity: 0 !important;*/
}
.circle.active{
  /*transition: opacity 1s ease-out;*/
  /*transition: opacity 0;*/
  /*transform: scale(2);*/

  opacity: 1;
  /*transition-delay: 400;*/
}
.inactive{
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25) !important;
  transition: opacity 0;
  background-color: rgba(255,255,255,0.3);  

  /*transform-origin: unset !important;
  transform: scale(2) !important;*/
  /*opacity: 0.2;*/
  /*transition-delay: 300;*/
}
.mapboxgl-ctrl-bottom-right{
  right: 40px !important;
}
.multiply{
  mix-blend-mode: multiply;
  box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.5);
  z-index: 0;
}
.big{
  mix-blend-mode: normal;
  box-shadow: 0px 0px 556px 300px rgba(0,0,0,0.2);
  overflow: hidden;
  z-index: 0;
}
.information{
  height: 100%;
  position: relative;
  z-index: 1;
  /*height: 100vh;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  padding-right: 5vw;
  padding-left: 5vw;
  background-color: #f1f1f1;
}
*::-moz-selection{
  color: rgba(0,0,0,0.8);
  background-color: rgba(0,0,0,0.1);
}
*::selection{
  color: rgba(0,0,0,0.8);
  background-color: rgba(0,0,0,0.1);
}
.winery-container{
display: block;
position: relative;
text-align: left;
padding: 1em 0;
border-top: dotted #5AAB6A 1px;
border-bottom: dotted #5AAB6A 1px;
/*background-color: red;*/
}
.winery-container > div{
  position: relative;
  display: block;
  float: left;
  width: 100%;
  margin: 0.3em 0;
}
.winery-container .label{
  font-weight: 700;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  padding-right: 0.4em;
  display: inline-block;
}
.winery-name{
  font-size: calc(10px + 1.5vw);
  /*font-family: 'Roboto';*/
  text-transform: uppercase;
  font-weight: 700;
}
.winery-region{
display: inline;
font-size: calc(10px + 0.6vw);
color: rgba(0,0,0,0.75);
padding-left: 0.5em;
text-transform: uppercase;
font-weight: 400;
}
.winery-desc{
  /*float: right;*/
  line-height: 1.35em;
}
.winery-where{
}
.winery-tel{
  width: auto !important;
  display: inline;
  padding-left: 0.6em;
  white-space: nowrap;
  /*float: right;*/
}
.winery-open{
  margin-top: 0 !important;
}
.winery-website{
  color: #61ad69 !important;
  display: inline-block;
  font-weight: 700;
  margin-top: 1em !important;
  text-decoration: none;
  color: unset;
  border-bottom: dotted transparent 1px;
}
.winery-website:hover{
  border-bottom: dotted #5AAB6A 1px;
}
.info-container{
  display: block;
  position: relative;
}
.images{
  display: block;
  width: 100%;
}
.winery-img{
  width: 100% !important;
  margin: 5vh 0;
  max-height: 33vh;
  height: 10vh;
}
.winery-img-bg{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin: 5vh 0;
  height: 30vh;
  max-height: 25vh;
}
.controls{
  position: absolute !important;
  z-index: 1;
  left: -200px;
  top: 0;
  min-width: 290px;
  zoom: 0.8;  
  transition: all 345ms cubic-bezier(0.0, 0.0, 0.2, 1);
}
.controls.active {
  left: -17px !important;
  transition: all 230ms cubic-bezier(0.0, 0.0, 0.2, 1);
}
.switch {

  height: 10px;
  left: 0px;
  top: 7px;
}
.switch.active *{
  color: #cb5a4d !important;
}
.switch label{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.switch label:after{
  content:"local_bar";
  position: relative;
  font-family:'Material Icons';
  font-size: 1.6em;
  line-height: 18px;
  top: 7px;
  padding-left: 15px;
  /*color: rgba(0,0,0,0.56);*/
  /*text-overflow: visible;*/
}
.switch-label{
  /*float: right;*/
}

/*/////////////////*/

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-enter-active {
  
  /*mine*/
  /*transition: all 355ms cubic-bezier(.29,.09,.17,1);*/

  /*google*/
  transition: all 355ms cubic-bezier(0.0, 0.0, 0.2, 1);
  transition-delay: .3s;
}
.slide-leave-active {
  /*mine*/
  /*transition: all 235ms cubic-bezier(0.19, 1, 0.22, 1);*/
  /*google*/
  transition: all 235ms cubic-bezier(0.4, 0.0, 1, 1);
}
.slide-enter, .slide-leave-to
/* .slide-fade-leave-active for <2.1.8 */ {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}





